<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process135"></a>

<p><br></p>
<p><span class="rvts17">支线：神龙迷宫（</span><span class="rvts50">The Gargoyle's Maze</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:533,Y:127</span><span class="rvts21">，地点：迷宫大门</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">进入秘密地下墓室</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1066.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">如果在</span><span class="rvts20"></span><span class="rvts21">布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯藏宝室</span><span class="rvts20"></span><span class="rvts21">支线任务中，获得了川普多依的诅咒戒指（戴上后会被诅咒），那么这里的石头恶魔会以为是雷克斯大人，便可以直接进入神龙迷宫。</span>
<br><span class="rvts21">如果在此未获得这枚诅咒戒指，将会被直接传送到墓地塔处（将会错过迷宫中很多获得好装备的机会）。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1067.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">会在迷宫的中心位置遇到一个不死族的历史学家。这个人懂得雷克斯生前的很多建筑秘密。只要扑灭这个历史学家身上的死灵火焰，便可以和它通话后，获得墙面上的传送门。（只要扑灭火焰即可，即便它死了也没关系）</span>
<br><span class="rvts21">扑灭火焰的方法十分简单，首先需要在地面创造一处血迹（使用精灵的献血献祭技能，或物理攻击队友，或使用血雨技能），然后把历史学家传送到血迹上。</span>
<br><span class="rvts21">这样它身上的血就会被熄灭。然而熄灭火焰并不代表它能存活下来，需要赶紧和它对话了解秘密（在它死之前）。或使用</span><span class="rvts20"></span><span class="rvts21">祝福</span><span class="rvts20"></span><span class="rvts21">技能，再次祝福它，它便不会死去。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1068.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">迷宫在刚开始确实很难，需要留意地面上的石头机关。某些机关可以关闭陷阱，某些机关可以打开铁门（这迷宫的铁门无法使用开锁技能打开，只能通过获得金色的骷髅头获得）。</span>
<br><span class="rvts21">理论上来说，迷宫区域存在</span><span class="rvts20">6</span><span class="rvts21">颗金色骷髅头，但实际上配合着传送手套，</span><span class="rvts20">4</span><span class="rvts21">颗就足以使到达秘密高塔处。（需要注意解密技巧，及时拆分队伍，及时传送队友）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1069.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">高塔里面机关较少，敌人也较少。但会遇到三个死灵法师，由于偷到了雷克斯的灵魂并滥用了源力，因此他们是永恒的。与他们战斗完后大概</span><span class="rvts20">20</span><span class="rvts21">秒，他们是会再次复活的。</span>
<br><span class="rvts21">因此尽可能的不要在他们的牌桌附近逗留太久。他们三个人中有一个人拿着大门的钥匙，取得钥匙后，迅速进入大门里面。想要彻底杀掉他们，详见支线任务</span><span class="rvts20"></span><span class="rvts21">比死亡还要糟糕的命运</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1070.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">有一处干涸的井，如果的角色中有会水元素中的</span><span class="rvts20"></span><span class="rvts21">下雨</span><span class="rvts20"></span><span class="rvts21">技能的话，可以使井充满水。里面的尸骨堆会提议捐些钱，换取较好的暗金装备。</span>
<br><span class="rvts21">最多捐献</span><span class="rvts20">100</span><span class="rvts21">多金币换一件稀有（非独特）装备，挺值得，可以不妨一试。（</span><span class="rvts20">4</span><span class="rvts21">个角色都可以和它进行对话，换取</span><span class="rvts20">4</span><span class="rvts21">件装备，超级聚划算！）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1071.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该高塔最终的藏宝地是密室。密室门外有一道源力墙，需要拥有源力的角色才能打开，开关就在门的旁边，与它对话即可。</span>
<br><span class="rvts21">另外在高塔深处的尽头，如果之前完成了</span><span class="rvts20"></span><span class="rvts21">维泽磨的灵柩罐</span><span class="rvts20"></span><span class="rvts21">支线，那么将再次遇到他。与他对话后，他会帮助打开这道门。</span>
<br><span class="rvts21">如果身上的已被解除禁锢的角色有源力值，那么可以直接和门旁边的开关对话便可打开。（如果没有的话，就悲剧了</span><span class="rvts20">...</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1072.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该高塔最终的藏宝地是密室。密室门外有一道源力墙，需要拥有源力的角色才能打开，开关就在门的旁边，与它对话即可。</span>
<br><span class="rvts21">密室中的宝物比较不错，这是该任务的最终奖励点。整个任务下来，可以至少获得</span><span class="rvts20">3</span><span class="rvts21">件暗金装和</span><span class="rvts20">2</span><span class="rvts21">件紫色装备，以及若干绿色装备。</span>
<br><span class="rvts21">这也是目前为止，在欢乐堡地图上最大的宝藏地图。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



